.image12Icon {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
}
.logoNew {
  position: relative;
  width: 226.57px;
  height: 120px;
  flex-shrink: 0;
}
.community {
  position: relative;
  letter-spacing: 0.05em;
  line-height: 160%;
  text-transform: capitalize;
  font-weight: 600;
}
.communityParent {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-13xl);
}
.frameGroup,
.logoNewParent {
  display: flex;
  flex-direction: row;
}
.frameGroup {
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-29xl);
}
.logoNewParent {
  width: 632px;
  flex-shrink: 0;
  align-items: flex-start;
  justify-content: space-between;
}
.mditwitterIcon {
  position: relative;
  width: 24px;
  height: 24px;
  flex-shrink: 0;
  overflow: hidden;
}
.mditwitterParent {
  flex-direction: row;
  justify-content: flex-end;
  gap: var(--gap-29xl);
}
.findUsParent,
.frameParent,
.mditwitterParent {
  display: flex;
  align-items: flex-start;
}
.findUsParent {
  flex-direction: column;
  justify-content: flex-start;
  gap: var(--gap-base);
}
.frameParent {
  position: absolute;
  bottom: 0;
  left: calc(50% - 720px);
  background-color: var(--color-midnightblue);
  width: 1440px;
  flex-direction: row;
  padding: var(--padding-29xl) var(--padding-61xl) var(--padding-77xl);
  box-sizing: border-box;
  justify-content: space-between;
  text-align: left;
  font-size: var(--p1sb-size);
  color: var(--wireframe2);
  font-family: var(--p1sb);
}
