@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@400;500;600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Mixed:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@600&display=swap");
@import url("https://fonts.googleapis.com/icon?family=Material+Icons");

body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --p1sb: Raleway;
  --font-inter: Inter;
  --font-mixed: Mixed;

  /* font sizes */
  --p1sb-size: 16px;
  --h6sb-size: 20px;
  --h3sb-size: 36px;
  --font-size-13xl: 32px;
  --h1sb-size: 48px;
  --font-size-11xl: 30px;

  /* Colors */
  --color-gray-100: rgba(14, 15, 45, 0.9);
  --color-deepskyblue: #54abee;
  --wireframe2: #fff;
  --color-skyblue: #78dfff;
  --color-midnightblue: rgba(0, 8, 77, 0.5);
  --contrast-black70: #999;

  /* Gaps */
  --gap-529xl: 548px;
  --gap-29xl: 48px;
  --gap-13xl: 32px;
  --gap-smi: 13px;
  --gap-base: 16px;
  --gap-36xl: 55px;
  --gap-5xs: 8px;
  --gap-xl: 20px;
  --gap-45xl: 64px;
  --gap-5xl: 24px;

  /* Paddings */
  --padding-5xs: 8px;
  --padding-61xl: 80px;
  --padding-base: 16px;
  --padding-13xl: 32px;
  --padding-29xl: 48px;
  --padding-77xl: 96px;
  --padding-45xl: 64px;

  /* border radiuses */
  --br-base: 16px;
  --br-5xs: 8px;
}
